
import 'styles/index.scss';



document.addEventListener('DOMContentLoaded', function(){

  const menu = document.getElementById('b-menu');
  const button = document.getElementById('b-button-icon');
  let iconMenu = document.getElementById('b-button-icon').src;
  let toggle = false;

  function classToggle() {
    menu.classList.toggle('b-close-menu');
    if (toggle === true) {
                console.log(document.getElementById('b-button-icon').src, 'close')
               document.getElementById('b-button-icon').src  = './../assets/images/burger.svg';
        }
         else {
              console.log(document.getElementById('b-button-icon').src, 'open')
               document.getElementById('b-button-icon').src = './../assets/images/close.svg';
       }
    toggle = !toggle;
  }
  button.addEventListener('click', classToggle);
});
